.Header {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.Header svg {
  display: inline-block;
  width: 50%;
}

.Header #dot {
  fill: #f4a142;
}

.Header .Detail {
  color: #f1f1f1;
  display: none;
  font-size: 90%;
}

.Header .Detail hr {
  height: 1px;
  background: #e1e1e1;
  border: none;
  margin-left: 25%;
  margin-right: 25%;
}

.Header a {
  color: #f4a142;
}

@media (min-width: 768px) {
  .Header {
    text-align: center;
    float: left;
    width: 30%;
    margin: 0;
  }
  .Header svg {
    margin-bottom: 2rem;
  }
  .Header .Detail {
    display: block;
  }
}
.Footer {
  color: #d1d1d1;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.PostModal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background: rgba(0,0,0,0.75);
}

.ModalBody {
  position: relative;
  top: -40px;
  background: #fff;
  border-radius: 3px;
  padding: 1.25rem;
}

.ModalBody .PostAvatar {
  float: left;
  width: 65px;
  margin-bottom: 0.5rem;
  margin-right: 1rem;
}

.ModalBody .PostAvatar.Retweet {
  border-radius: 50%;
  overflow: hidden;
}

.ModalBody .PostAvatar.Retweet img {
  display: block;
  width: 100%;
  height: auto;
}

.ModalBody .PostMeta p {
  display: inline-block;
  margin-top: 1rem;
}

.ModalBody .Close {
  position: absolute;
  top: -40px;
  right: 0;
}

.ModalBody .Close svg {
  fill: #e1e1e1;
  width: 20px;
  -webkit-transition: -webkit-transform 91ms ease-out;
  transition: -webkit-transform 91ms ease-out;
  transition: transform 91ms ease-out;
  transition: transform 91ms ease-out, -webkit-transform 91ms ease-out;
}

.ModalBody .Close:hover svg {
  fill: #f4a142;
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
}

@media (min-width: 640px) {
  .ModalBody {
    width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}
.Post {
  position: relative;
  background: #fff;
  min-height: 90px;
  padding: 1rem 0.75rem 1rem 80px;
  border-bottom: 1px solid #e1e1e1;
  -webkit-transition: background 173ms ease-out;
  transition: background 173ms ease-out;
}

.Post.First {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.Post.Last {
  border-bottom: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.Post > .PostAvatar {
  position: absolute;
  left: 0.75rem;
  width: 56px;
}

.Post > .PostAvatar.Retweet {
  border-radius: 50%;
  overflow: hidden;
}

.Post > .PostAvatar.Retweet img {
  display: block;
  width: 100%;
  height: auto;
}

.Post p {
  margin: 0;
}

.Post .PostMeta {
  font-weight: 400;
  font-size: 75%;
  color: #bbb;
}

.Post .PostControls {
  padding-top: 0.5rem;
  color: #bbb;
}

.Post .PostControls svg {
  width: 16px;
  fill: #bbb;
}

.Post .PostControls svg:hover {
  fill: #555;
}

.Post:hover {
  background: #f9f9f9;
}

.spinner {
  width: 75px;
  height: 75px;
  position: relative;
  margin: 100px auto;
}

.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
          animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 { -webkit-animation-delay: -1.0s; animation-delay: -1.0s }

@-webkit-keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0); transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0); transform: scale(1.0) }
}

@keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0); transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0); transform: scale(1.0) }
}
.TobytarWrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.Tobytar {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
  margin: 10%;
}

.Tobytar #Eyes {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  text-align: center;
}

